.contact-form__title, .fields__title, .relationship__title, .trademarks__title, .products__title, .scheme__title {
  text-transform: uppercase;
  font-style: normal;
  font-weight: bold;
  font-weight: 400;
  font-size: 60px;
  line-height: 120%;
  position: relative;
  padding-top: 30px;
}

.fields__title::before, .relationship__title::before, .trademarks__title::before, .products__title::before, .scheme__title::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: #5C8EE6;
  height: 1px;
  width: 150px;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

