.btn {
  cursor: pointer;
  margin: 0;
  padding: 25px 60px;
  font-weight: 600;
  font-size: 16px;
  display: inline-block;
  color: #fff;
}
.btn-line {
  font-weight: 600;
  font-size: 16px;
  line-height: 160%;
  color: #5C8EE6;
  position: relative;
  padding-left: 60px;
  transition: 0.3s;
}
.btn-line::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  height: 1px;
  width: 40px;
  background: #5C8EE6;
  transition: 0.3s;
}
.btn-line:hover {
  padding-left: 80px;
}
.btn-line:hover::before {
  width: 60px;
}
.btn-primary {
  background-color: #5C8EE6;
  transition: 0.3s;
}
.btn-primary:hover {
  background-color: #3A71D1;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
